// Validate required field
const general_required = (value) => {
    return !!value || 'This field is required.'
};

// Validate numeric
const numeric = (value) => {
    return /[^\D]{5}/.test(value) || 'It must be a valid numeric.'
};

// Validate word
const word = (value) => {
    return /[a-zA-Z\s]{4,}]/.test(value) || 'It must be a meaningful word.'
}

// Validate email
const email = (value) => {
    return /.+@.+\..+/.test(value) || 'E-mail must be valid'
}

export default {
    general_required,
    numeric,
    word,
    email
}