<template>
    <v-form @submit.prevent="submitForm" class="invoice-content">
        <v-container>
            <v-row justify="center">
                <v-col
                    cols="12"
                    sm="10"
                    md="8"
                    lg="6"
                >

                </v-col>

            </v-row>
            <v-card>
                <!-- Bill From  -->
                <v-card-title class="pb-0">Bill From:</v-card-title>
                <v-card-text>
                    <v-text-field  label="Street Address" v-model="billerStreetAddress" required></v-text-field>
                    <v-row>
                        <v-col  md="4" sm="4">
                            <v-text-field  label="City" v-model="billerCity" :rules="wordRules" required></v-text-field>
                        </v-col>
                        <v-col  md="4" sm="4">
                            <v-text-field  label="Post Code" v-model="billerZipCode" :rules="postcodeRules" required></v-text-field>
                        </v-col>
                        <v-col  md="4" sm="4">
                            <v-text-field  label="Country" v-model="billerCountry" :rules="wordRules" required></v-text-field>
                        </v-col>
                    </v-row>
                </v-card-text>
                <v-spacer></v-spacer>

                <!-- Bill To  -->
                <v-card-title class="pb-0">Bill To:</v-card-title>
                <v-card-text>
                    <v-text-field  label="Client's Name" v-model="clientName" required></v-text-field>
                    <v-text-field  label="Client's Email" v-model="clientEmail" :rules="emailRules" required></v-text-field>
                    <v-text-field  label="Street Address" v-model="clientStreetAddress" required></v-text-field>
                    <v-row>
                        <v-col  md="4" sm="4">
                            <v-text-field  label="City" v-model="clientCity" :rules="wordRules" required></v-text-field>
                        </v-col>
                        <v-col  md="4" sm="4">
                            <v-text-field  label="Post Code" v-model="clientZipCode" :rules="postcodeRules" required></v-text-field>
                        </v-col>
                        <v-col  md="4" sm="4">
                            <v-text-field  label="Country" v-model="clientCountry" :rules="wordRules" required></v-text-field>
                        </v-col>
                    </v-row>
                </v-card-text>

                <!-- Invoice Work Details  -->
                <v-card-text>
                    <v-row>
                        <v-col md="5" sm="6">
                            <v-text-field  label="Invoice Date" v-model="invoiceDate" disabled></v-text-field>
                        </v-col>
                        <v-col md="5" offset-md="2" sm="6">
                            <v-text-field  label="Payment Date" v-model="paymentDueDate" disabled></v-text-field>
                        </v-col>
                    </v-row>
                    <v-select :items="listTerms" label="Payment Terms" v-model="paymentTerms"></v-select>
                    <v-text-field  label="Product's Description" v-model="productDescription"></v-text-field>
                </v-card-text>
                <v-card-title>Item List</v-card-title>
                <v-card-text>
                    <v-simple-table dense>
                        <template v-slot:default>
                            <thead>
                                <tr>
                                    <th
                                        v-for="(item, index) in tableHead"
                                        :key=index
                                    >
                                        {{ item.text }}
                                    </th>
                                </tr>
                            </thead>
                            <tbody>
                                <tr
                                    v-for="(item, index) in invoiceItemList"
                                    :key="index"
                                >
                                    <td><v-text-field v-model="item.name"></v-text-field></td>
                                    <td>
                                        <v-text-field v-model="item.qty" type="number" @change="updateTotal"></v-text-field>
                                    </td>
                                    <td><v-text-field v-model="item.price" prepend-icon="mdi-currency-usd" readonly></v-text-field></td>
                                    <td>
                                        <v-text-field :value="(item.total = item.qty * item.price).toFixed(2)" readonly>
                                            <template v-slot:prepend>
                                                <v-icon>mdi-currency-usd</v-icon>
                                            </template>
                                            <template v-slot:append-outer>
                                                <v-icon color="red lighten-2" @click="deleteInvoiceItem(item.id)">mdi-delete-outline</v-icon>
                                            </template>
                                        </v-text-field>
                                    </td>
                                </tr>
                            </tbody>
                        </template>
                    </v-simple-table>
                    <div id="addNewItem" @click="addNewInvoiceItem" class="py-1 text-center text-uppercase text--secondary text-button">
                        <v-icon color="light-blue lighten-3">mdi-plus</v-icon>
                        Add New Item
                    </div>
                </v-card-text>

                <!-- Save/Exit Actions  -->
                <v-card-actions class="mt-5">
                    <v-btn
                        class="ma-2"
                        color="error"
                        outlined
                        @click="closeInvoice"
                    >Discard</v-btn>
                    <v-spacer></v-spacer>
                    <v-btn
                        class="ma-2"
                        outlined
                        color="grey"
                        @click="saveDraft"
                    >Save Draft</v-btn>
                    <v-btn
                        class="ma-2"
                        color="primary"
                        dark
                        @click="publishInvoice"
                    >Create Invoice</v-btn>
                </v-card-actions>
            </v-card>
        </v-container>
    </v-form>
</template>
<script>
import { uid } from 'uid';
import validate from '../../data/validation';
import db from '../../firebase/firebaseinit';
export default {
    name: "invoiceModal",
    data() {
        return {
            billerStreetAddress:    null,
            billerCity:             null,
            billerZipCode:          null,
            billerCountry:          null,
            clientName:             null,
            clientEmail:            null,
            clientStreetAddress:    null,
            clientCity:             null,
            clientZipCode:          null,
            clientCountry:          null,
            invoiceDateUnix:        null,
            invoiceDate:            null,
            paymentTerms:           null,
            paymentDueDateUnix:     null,
            paymentDueDate:         null,
            productDescription:     null,
            invoicePending:         null,
            invoiceDraft:           null,
            invoiceItemList:        [{name: 'abdc', qty: 12, price: 5.9, total: 12}],
            invoiceTotal:           0,

            dateOptions:            { year: "numeric", month: "short", day: "numeric"},
            listTerms:              [
                                        { text: 'Next 30 days', value: '30' },
                                        { text: 'Next 45 days', value: '45' },
                                        { text: 'Next 60 days', value: '60' },
                                        { text: 'Next 75 days', value: '75' },
                                        { text: 'Next 90 days', value: '90' }
                                    ],
            tableHead:              [   { text: 'Item Name',    width: '60%'},
                                        { text: 'Qty',          width: '70px'},
                                        { text: 'Price',        width: '50px'},
                                        { text: 'Total',        width: '20%'}
                                    ],
            // All Rules
            postcodeRules: [
                v => validate.general_required(v),
                v => validate.numeric(v)
            ],
            wordRules: [
                v => validate.general_required(v),
                v => validate.word(v)
            ],
            emailRules: [
                v => validate.general_required(v),
                v => validate.email(v)
            ]
        };
    },
    methods: {
        deleteInvoiceItem(id) {
            this.invoiceItemList = this.invoiceItemList.filter(item => item.id !== id)
        },
        addNewInvoiceItem() {
            this.invoiceItemList.push({
                id:         uid(),
                name:       '',
                qty:        0,
                price:      0,
                total:      0,
            })
        },
        updateTotal(event) {
            this.item.qty = event.target.value;
            this.item.total = this.item.qty * this.item.price
        },
        calculateInvoiceTotal() {
            this.invoiceTotal = 0;
            this.invoiceItemList.forEach(item => {
                this.invoiceTotal += item.total;
            });
        },
        closeInvoice(){
            this.$router.push('/invoice');
        },
        publishInvoice(){
            this.invoicePending = true;
        },
        saveDraft(){
            this.invoiceDraft = true;
        },
        async uploadInvoice(){
            if (this.invoiceItemList.length <= 0) {
                alert('Please ensure you filled out work items !');
                return;
            }
            this.calculateInvoiceTotal();

            const database = db.collection('invoices').doc()

            await database.set({
                invoiceId:              uid(6),
                billerStreetAddress:    this.billerStreetAddress,
                billerCity:             this.billerCity,
                billerZipCode:          this.billerZipCode,
                billerCountry:          this.billerCountry,
                clientName:             this.clientName,
                clientEmail:            this.clientEmail,
                clientStreetAddress:    this.clientStreetAddress,
                clientCity:             this.clientCity,
                clientZipCode:          this.clientZipCode,
                clientCountry:          this.clientCountry,
                invoiceDateUnix:        this.invoiceDateUnix,
                invoiceDate:            this.invoiceDate,
                paymentTerms:           this.paymentTerms,
                paymentDueDateUnix:     this.paymentDueDateUnix,
                paymentDueDate:         this.paymentDueDate,
                productDescription:     this.productDescription,
                invoiceItemList:        this.invoiceItemList,
                invoiceTotal:           this.invoiceTotal,
                invoicePending:         this.invoicePending,
                invoiceDraft:           this.invoiceDraft,
                invoicePaid:            null
            })

        },
        submitForm(){
            this.uploadInvoice();
        }
    },
    created() {
        // get current date for invoice date field
        this.invoiceDateUnix = Date.now();
        this.invoiceDate = new Date(this.invoiceDateUnix).toLocaleDateString("en-us", this.dateOptions)
    },
    watch: {
        paymentTerms() {
            const futureDate = new Date();
            this.paymentDueDateUnix = futureDate.setDate(futureDate.getDate() + parseInt(this.paymentTerms));
            this.paymentDueDate = new Date(this.paymentDueDateUnix).toLocaleDateString("en-us", this.dateOptions)
        }
    }
}
</script>

<style>
#addNewItem{
    /* Light-Blue Lighten-3  */
    color: #29B6F6 !important;
    border: 1px dashed #29B6F6;
    border-radius: 3px;
}
#addNewItem:hover{
    cursor: pointer;
    border-color: #81D4FA;
    color: #81D4FA !important;
}
</style>